"use client";

import type React from "react";

import { useState, useEffect, useRef, useCallback } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card } from "@/components/ui/card";
import {
  Search,
  Sparkles,
  ArrowRight,
  Clock,
  TrendingUp,
  Lightbulb,
  MicIcon,
} from "lucide-react";
import SiriWave from "./ui/Wave";


interface SearchSuggestion {
  id: string;
  text: string;
  category: "recent" | "trending" | "suggested";
  icon: React.ReactNode;
}

interface EnhancedSearchBarProps {
  onSearch?: (query: string) => void;
  placeholder?: string;
  className?: string;
}

export default function EnhancedSearchBar({
  onSearch,
  placeholder = "Ask me anything... What's on your mind?",
  className = "",
}: EnhancedSearchBarProps) {
  const [searchQuery, setSearchQuery] = useState("");
  const [isListening, setIsListening] = useState(false);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [filteredSuggestions, setFilteredSuggestions] = useState<
    SearchSuggestion[]
  >([]);
  const [selectedIndex, setSelectedIndex] = useState(-1);
  const inputRef = useRef<HTMLInputElement>(null);
  const suggestionsRef = useRef<HTMLDivElement>(null);

  const suggestions: SearchSuggestion[] = [
    {
      id: "1",
      text: "How to build a React application with Next.js?",
      category: "trending",
      icon: <TrendingUp className="h-4 w-4" />,
    },
    {
      id: "2",
      text: "Explain machine learning in simple terms",
      category: "suggested",
      icon: <Lightbulb className="h-4 w-4" />,
    },
    {
      id: "3",
      text: "Write a professional email template",
      category: "recent",
      icon: <Clock className="h-4 w-4" />,
    },
    {
      id: "4",
      text: "Create a marketing strategy for a startup",
      category: "trending",
      icon: <TrendingUp className="h-4 w-4" />,
    },
    {
      id: "5",
      text: "Best practices for web development",
      category: "suggested",
      icon: <Lightbulb className="h-4 w-4" />,
    },
    {
      id: "6",
      text: "How to optimize website performance?",
      category: "recent",
      icon: <Clock className="h-4 w-4" />,
    },
  ];

  // Filter suggestions based on search query
  useEffect(() => {
    if (searchQuery.trim()) {
      const filtered = suggestions.filter((suggestion) =>
        suggestion.text.toLowerCase().includes(searchQuery.toLowerCase())
      );
      setFilteredSuggestions(filtered.slice(0, 4));
    } else {
      setFilteredSuggestions(suggestions.slice(0, 6));
    }
    setSelectedIndex(-1);
  }, [searchQuery]);

  // Handle keyboard navigation
  const handleKeyDown = useCallback(
    (e: KeyboardEvent) => {
      if (!showSuggestions) return;

      switch (e.key) {
        case "ArrowDown":
          e.preventDefault();
          setSelectedIndex((prev) =>
            prev < filteredSuggestions.length - 1 ? prev + 1 : prev
          );
          break;
        case "ArrowUp":
          e.preventDefault();
          setSelectedIndex((prev) => (prev > 0 ? prev - 1 : -1));
          break;
        case "Enter":
          e.preventDefault();
          if (selectedIndex >= 0) {
            handleSuggestionClick(filteredSuggestions[selectedIndex]);
          } else {
            handleSearch();
          }
          break;
        case "Escape":
          setShowSuggestions(false);
          setSelectedIndex(-1);
          break;
      }
    },
    [showSuggestions, selectedIndex, filteredSuggestions]
  );

  useEffect(() => {
    document.addEventListener("keydown", handleKeyDown);
    return () => document.removeEventListener("keydown", handleKeyDown);
  }, [handleKeyDown]);

  // Voice recognition
  const toggleVoiceRecognition = () => {
    if (!isListening) {
      startVoiceRecognition();
    } else {
      stopVoiceRecognition();
    }
  };

  const startVoiceRecognition = () => {
    if ("webkitSpeechRecognition" in window || "SpeechRecognition" in window) {
      const SpeechRecognition =
        window.SpeechRecognition || window.webkitSpeechRecognition;
      const recognition = new SpeechRecognition();

      recognition.continuous = false;
      recognition.interimResults = true;
      recognition.lang = "en-US";

      recognition.onstart = () => setIsListening(true);
      recognition.onresult = (event: any) => {
        const transcript = Array.from(event.results)
          .map((result: any) => result[0])
          .map((result) => result.transcript)
          .join("");
        setSearchQuery(transcript);
      };
      recognition.onerror = () => setIsListening(false);
      recognition.onend = () => setIsListening(false);

      recognition.start();
    }
  };

  const stopVoiceRecognition = () => {
    setIsListening(false);
  };

  const handleSearch = () => {
    if (searchQuery.trim()) {
      onSearch?.(searchQuery);
      setShowSuggestions(false);
    }
  };

  const handleSuggestionClick = (suggestion: SearchSuggestion) => {
    setSearchQuery(suggestion.text);
    setShowSuggestions(false);
    onSearch?.(suggestion.text);
  };

  const getCategoryColor = (category: SearchSuggestion["category"]) => {
    switch (category) {
      case "trending":
        return "text-orange-500";
      case "recent":
        return "text-blue-500";
      case "suggested":
        return "text-green-500";
      default:
        return "text-gray-500";
    }
  };

  return (
    <div className={`relative max-w-2xl mx-auto ${className}`}>
      <div className="relative">
        {/* Search Icon */}
        <div className="absolute left-4 top-1/2 -translate-y-1/2 text-sky-500 z-10">
          <Search className="h-6 w-6" />
        </div>

        {/* Input Field */}
        <Input
          ref={inputRef}
          placeholder={placeholder}
          className="h-16 pl-14 pr-32 text-lg border-2 border-sky-200 focus:border-sky-400 rounded-2xl shadow-lg bg-white/90 backdrop-blur-sm transition-all duration-200 focus:shadow-xl"
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          onFocus={() => setShowSuggestions(true)}
          onBlur={() => {
            // Delay hiding suggestions to allow clicks
            setTimeout(() => setShowSuggestions(false), 150);
          }}
        />

        {/* Voice Wave Animation */}
        {isListening && (
          <div className="absolute left-14 right-32 top-1/2 -translate-y-1/2 z-10">
            <SiriWave
              isWaveMode={isListening}
              height={30}
              colors={["#0ea5e9", "#3b82f6", "#6366f1", "#8b5cf6"]}
              style="hybrid"
            />
          </div>
        )}

        {/* Action Buttons */}
        <div className="absolute right-2 top-1/2 -translate-y-1/2 flex gap-2 z-10">
          <Button
            size="lg"
            variant="ghost"
            className={`h-12 w-12 rounded-xl transition-all duration-200 ${
              isListening
                ? "bg-red-500 hover:bg-red-600 text-white animate-pulse"
                : "bg-sky-100 hover:bg-sky-200 text-sky-600"
            }`}
            onClick={toggleVoiceRecognition}
          >
            <MicIcon className="h-5 w-5" />
          </Button>
          <Button
            size="lg"
            className="bg-sky-600 hover:bg-sky-700 text-white rounded-xl px-6 shadow-lg hover:shadow-xl transition-all duration-200 hover:scale-105"
            onClick={handleSearch}
            disabled={!searchQuery.trim()}
          >
            <Sparkles className="h-5 w-5 mr-2" />
            Search
          </Button>
        </div>
      </div>

      {/* Suggestions Dropdown */}
      <AnimatePresence>
        {showSuggestions && filteredSuggestions.length > 0 && (
          <motion.div
            ref={suggestionsRef}
            initial={{ opacity: 0, y: -10, scale: 0.95 }}
            animate={{ opacity: 1, y: 0, scale: 1 }}
            exit={{ opacity: 0, y: -10, scale: 0.95 }}
            transition={{ duration: 0.2, ease: "easeOut" }}
            className="absolute top-full left-0 right-0 mt-2 z-50"
          >
            <Card className="p-2 bg-white/95 backdrop-blur-md shadow-xl border-sky-200 rounded-2xl">
              <div className="space-y-1">
                {filteredSuggestions.map((suggestion, index) => (
                  <motion.button
                    key={suggestion.id}
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ delay: index * 0.05 }}
                    className={`w-full text-left p-3 rounded-xl transition-all duration-200 flex items-center gap-3 group ${
                      selectedIndex === index
                        ? "bg-sky-50 border border-sky-200"
                        : "hover:bg-gray-50"
                    }`}
                    onClick={() => handleSuggestionClick(suggestion)}
                  >
                    <div
                      className={`${getCategoryColor(
                        suggestion.category
                      )} group-hover:scale-110 transition-transform`}
                    >
                      {suggestion.icon}
                    </div>
                    <span className="text-gray-700 group-hover:text-gray-900 flex-1">
                      {suggestion.text}
                    </span>
                    <ArrowRight className="h-4 w-4 text-gray-400 opacity-0 group-hover:opacity-100 transition-opacity" />
                  </motion.button>
                ))}
              </div>
            </Card>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}
