"use client";

import { motion } from "framer-motion";

interface WaveBackgroundProps {
  className?: string;
  opacity?: number;
}

export default function WaveBackground({
  className = "",
  opacity = 0.1,
}: WaveBackgroundProps) {
  return (
    <div
      className={`absolute inset-0 overflow-hidden ${className}`}
      aria-hidden="true"
    >
      <svg
        className="absolute inset-0 w-full h-full"
        viewBox="0 0 1200 800"
        preserveAspectRatio="xMidYMid slice"
        fill="none"
      >
        <defs>
          <linearGradient
            id="wave-gradient-1"
            x1="0%"
            y1="0%"
            x2="100%"
            y2="100%"
          >
            <stop offset="0%" stopColor="#0ea5e9" stopOpacity={opacity} />
            <stop
              offset="50%"
              stopColor="#3b82f6"
              stopOpacity={opacity * 0.8}
            />
            <stop
              offset="100%"
              stopColor="#6366f1"
              stopOpacity={opacity * 0.6}
            />
          </linearGradient>
          <linearGradient
            id="wave-gradient-2"
            x1="0%"
            y1="100%"
            x2="100%"
            y2="0%"
          >
            <stop offset="0%" stopColor="#8b5cf6" stopOpacity={opacity * 0.7} />
            <stop
              offset="50%"
              stopColor="#06b6d4"
              stopOpacity={opacity * 0.5}
            />
            <stop
              offset="100%"
              stopColor="#10b981"
              stopOpacity={opacity * 0.3}
            />
          </linearGradient>
        </defs>

        {/* Animated Wave Paths */}
        <motion.path
          d="M0,400 C300,300 600,500 900,400 C1050,350 1150,450 1200,400 L1200,800 L0,800 Z"
          fill="url(#wave-gradient-1)"
          initial={{ pathLength: 0, opacity: 0 }}
          animate={{ pathLength: 1, opacity: 1 }}
          transition={{ duration: 2, ease: "easeInOut" }}
        />

        <motion.path
          d="M0,500 C300,400 600,600 900,500 C1050,450 1150,550 1200,500 L1200,800 L0,800 Z"
          fill="url(#wave-gradient-2)"
          initial={{ pathLength: 0, opacity: 0 }}
          animate={{ pathLength: 1, opacity: 1 }}
          transition={{ duration: 2.5, delay: 0.5, ease: "easeInOut" }}
        />

        {/* Floating Elements */}
        <motion.circle
          cx="200"
          cy="200"
          r="3"
          fill="#0ea5e9"
          opacity={opacity * 2}
          animate={{
            y: [0, -20, 0],
            opacity: [opacity * 2, opacity * 4, opacity * 2],
          }}
          transition={{
            duration: 3,
            repeat: Number.POSITIVE_INFINITY,
            ease: "easeInOut",
          }}
        />

        <motion.circle
          cx="800"
          cy="150"
          r="2"
          fill="#3b82f6"
          opacity={opacity * 2}
          animate={{
            y: [0, -15, 0],
            opacity: [opacity * 2, opacity * 3, opacity * 2],
          }}
          transition={{
            duration: 4,
            delay: 1,
            repeat: Number.POSITIVE_INFINITY,
            ease: "easeInOut",
          }}
        />

        <motion.circle
          cx="1000"
          cy="300"
          r="4"
          fill="#6366f1"
          opacity={opacity * 1.5}
          animate={{
            y: [0, -25, 0],
            opacity: [opacity * 1.5, opacity * 3, opacity * 1.5],
          }}
          transition={{
            duration: 3.5,
            delay: 2,
            repeat: Number.POSITIVE_INFINITY,
            ease: "easeInOut",
          }}
        />
      </svg>
    </div>
  );
}
